@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app {
  /* display: flex; */
  /* flex-direction: column; */

  /* align-items: center; */
  margin-top: 64px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  /* display: flex;
	flex-direction: column; */
  /* justify-content: center; */
  /* align-items: center; */
  text-align: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

.coin-row {
  align-items: center;
  min-height: 80px;
  border-bottom: 1px solid #d7d7d7;
  text-align: center;
}

.coin {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin h1 {
  font-size: 16px;
}

.coin-symbol {
  text-transform: uppercase;
}

.red {
  font-weight: 800;
  color: #f00606;
}

.green {
  font-weight: 800;
  color: #11d811;
}

h1,
p {
  margin-bottom: 0;
}

@media only screen and (max-width: 768px) {
  .coin-row {
    padding: 20px 0;
  }
}

